* {
  margin: 0;
  padding: 0;
  font-family: "Nunito";
}

a {
  text-decoration: none;
  color: #000;
}

li {
  list-style: none;
}

button {
  background: none;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 20px;
  max-width: 1200px;
}

.d-flex {
  display: flex;
}